import getUuid from 'uuid-by-string'
// Adjust the number of '../' depending on your exact folder structure
// If page is in src/app/item/[id]/page.jsx, then data is ../../../data.json
import images from '../../../data.json' 

export async function generateStaticParams() {
  // 1. Check if images exist
  if (!images || images.length === 0) {
    return []
  }

  // 2. Map over images to generate the paths
  // IMPORTANT: The key 'id' must match the folder name '[id]'
  return images.map((img) => ({
    id: getUuid(img.url), 
  }))
}

export default function ItemPage({ params }) {
  const { id } = params
  
  // Optional: Find the specific image data to display SEO tags
  const image = images.find(img => getUuid(img.url) === id)

  return (
    <div className="absolute top-0 left-0 w-full h-full flex items-center justify-center pointer-events-none text-white z-0">
      {/* This div is behind the 3D canvas. 
          You can use it for SEO hidden text or metadata if needed. */}
      <h1 className="sr-only">Item {id}</h1>
    </div>
  )
}
